import React, { useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import {googleAuth, registration} from '../../Api/auth';
import { isAuthenticated, authenticate } from '../../utils/auth';
import "./User.css"
import { notify } from '../../utils/notification'
import { Alert } from "antd";
import NavBar from "../../layouts/NavBar";
import Footer from "../Main/Footer";
import { GoogleLogin } from 'react-google-login';
import Lottie from "react-lottie";
import animationData from "../../assets/lotte/50124-user-profile.json";
import { AiFillFacebook, AiOutlineGoogle } from "react-icons/ai";
import {useForm} from "react-hook-form";


const setPasswordAlert = () => (
    notify('Password Miss-matched')
)

const Register = () => {
    const [values, setValues] = useState({
        username: '',
        email: '',
        phonenumber: '',
        password: '',
        success: false,
        error: false,
        disabled: false,
    });

    const { username, email, password, phonenumber, success, error, repassword, disabled } = values;

    const handleChange = e => {
        setValues({
            ...values,
            error: false,
            [e.target.name]: e.target.value
        })
    }

    const {register, watch, setError, formState: {errors}, reset} = useForm();
    const handleSubmit = e => {
        reset()
        e.preventDefault();
        console.log(values)
        setValues({ ...values, error: false, disabled: true });
        if (password === repassword) {
            registration({ username, email, phonenumber, password  })
                .then(response => {
                    authenticate(response.data.token, () => {
                        setValues({
                            username: '',
                            email: '',
                            phonenumber: '',
                            password: '',
                            success: true,
                            error: false,
                            disabled: false,
                        })
                    })
                })
                .catch(function (error) {
                    let errMsg = 'Something went wrong!';
                    if (error.response) {
                        errMsg = error.response.data;
                    } else {
                        errMsg = 'Something went wrong!';
                    }
                    setValues({ ...values, error: errMsg, disabled: false })
                    if (error.response) {
                        // Request made and server responded
                        console.log(error.response.data);
                        console.log(error.response.status);
                        console.log(error.response.headers);
                        // if (error.response.data==="User already register!")
                        // setError("credential", {
                        //     type: "email",
                        //     message: "Something went wrong",
                        // });
                        // if (error.response.data==="Phone Number already exists!")
                        //     setError("credential", {
                        //         type: "email",
                        //         message: "Something went wrong",
                        //     });
                    } else if (error.request) {
                        // The request was made but no response was received
                        console.log(error.request);
                    } else {
                        // Something happened in setting up the request that triggered an Error
                        console.log('Error', error.message);
                    }

                })
                .catch(err => {
                    let errMsg = 'Something went wrong!';
                    if (err.response) {
                        errMsg = err.response.data;
                    } else {
                        errMsg = 'Something went wrong!';
                    }
                    setValues({ ...values, error: errMsg, disabled: false })
                })
        } else {
            setPasswordAlert()
            setValues({ ...values, disabled: false })
        }

    }

    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: animationData,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice"
        }
    };


    const responseGoogle = (response) => {
        console.log(response);
        googleAuth(response).then(res=> {
            authenticate(res.data.token, ()=> {
                console.log("ok")
                reset()
            })
        })            .catch(function (error) {
            if (error.response) {
                setError("credential", {
                    type: "credential",
                    message: "This account can not be logged in by google",
                });
                // setError("password", {
                //     type: "credential",
                //     message: "Email or password is wrong",
                // });
                console.log(error.response.data);
                console.log(error.response.status);
                console.log(error.response.headers);
            } else if (error.request) {
                // The request was made but no response was received
                console.log(error.request);
            } else {
                // Something happened in setting up the request that triggered an Error
                console.log('Error', error.message);
                setError("email", {
                    type: "invalid credential",
                    message: "Something went wrong",
                });
                setError("password", {
                    type: "invalid credential",
                    message: "Something went wrong",
                });
            }

        });

    }

    function failureGoogle(response) {
        setError("credential", {
            type: "credential",
            message: "Google log in failed",
        });
    }

    const signUpForm = () => (
        <>
            <NavBar />
            <div className="mx-auto" data-v-791b20d9>
                <div data-v-791b20d9>
                    <div data-v-791b20d9>
                        <div
                            className="min-w-screen bg-gray-200 flex items-center justify-center md:px-5 py-10 md:py-6">
                            <div className="bg-white text-gray-500 w-full overflow-hidden">
                                <div className="md:flex w-full">
                                    <div className="hidden md:block w-1/3  py-10 px-10">
                                        <div style={{ marginTop: "100Px" }}>
                                            <Lottie
                                                options={defaultOptions}
                                                height={500}
                                            />
                                        </div>
                                    </div>
                                    <div className="w-full md:w-2/3 py-10 px-5 md:px-10 relative">
                                        <div className="text-center mb-10">
                                            <h1 className="text-xl md:text-2xl font-bold leading-tight text-gray-900 text-left">
                                                Create new account
                                            </h1>
                                        </div>
                                        <div className="mb-7">
                                            <button type="button"
                                                className="w-full block bg-white hover:bg-gray-100 focus:bg-gray-100 text-gray-900 font-semibold rounded-lg px-4 py-3 border border-gray-300">
                                                <div className="flex items-center justify-center">
                                                    <div>Login with </div>
                                                    <div className="flex-c-m">

                                                        <GoogleLogin
                                                            clientId="645786293828-kmkdc1hrcf5cpajci88senmkcb5rn1j5.apps.googleusercontent.com"
                                                            render={renderProps => (
                                                                <button className="login100-social-item " style={{color: '#299078', borderColor: '#299078'}} onClick={renderProps.onClick} disabled={renderProps.disabled} > <AiOutlineGoogle/></button>
                                                            )}
                                                            buttonText="Login"
                                                            onSuccess={responseGoogle}
                                                            onFailure={failureGoogle}
                                                            cookiePolicy={'single_host_origin'}
                                                        />
                                                    </div>

                                                </div>
                                            </button>
                                        </div>
                                        <form onSubmit={handleSubmit}>
                                            <div className="flex -mx-3 flex-wrap">
                                                <div className="w-full md:w-1/2 px-3 mb-5">
                                                    <div><label className="block text-gray-700">Username</label> <input
                                                        type="text"
                                                        placeholder="Enter Username" autoFocus="autofocus" autoComplete
                                                        name="username"
                                                        value={username}
                                                        className="tw_form_input" required onChange={handleChange} />
                                                    </div>

                                                </div>
                                                <div className="w-full md:w-1/2 px-3 mb-5">
                                                    <div><label className="block text-gray-700">Phone Number</label>
                                                        <input
                                                            placeholder="Phone Number" autoFocus="autofocus"
                                                            autoComplete
                                                            type="tel" value={phonenumber} name="phonenumber"
                                                            className="tw_form_input" required onChange={handleChange} />
                                                    </div>
                                                    {errors.credential?.type === 'phoneNumber' && <span style={{color: "red", mt:2}}>This Phone Number has already been registered, try a new one</span>}
                                                </div>
                                            </div>
                                            <div className="flex -mx-3">
                                                <div className="w-full px-3 mb-5">
                                                    <div><label className="block text-gray-700">Email</label> <input
                                                        type="email"
                                                        placeholder="Enter Email" autoComplete value={email}
                                                        name="email" required onChange={handleChange}
                                                        className="tw_form_input" /></div>
                                                    {errors.credential?.type === 'email' && <span style={{color: "red"}}>This Email has already been registered, try a new one</span>}

                                                </div>
                                            </div>
                                            <div className="flex flex-wrap -mx-3">
                                                <div className="w-full md:w-1/2 px-3 mb-5">
                                                    <div><label className="block text-gray-700">Password</label> <input
                                                        type="password"
                                                        placeholder="Enter Password" autoComplete value={password}
                                                        name="password" required onChange={handleChange}
                                                        className="tw_form_input" /></div>

                                                </div>
                                                <div className="w-full md:w-1/2 px-3 mb-5">
                                                    <div><label className="block text-gray-700">Confirm Password</label>
                                                        <input type="password"

                                                            placeholder="Re-enter Password" autoComplete

                                                            value={repassword}
                                                            name="repassword" required onChange={handleChange}
                                                            className="tw_form_input" /></div>

                                                </div>
                                            </div>

                                            <div className="flex -mx-3">
                                                <div className="w-full px-3 mb-5">
                                                    <button type="submit" disabled={disabled}
                                                        className="w-full block text-white font-semibold rounded-lg  x-4 py-3 mt-6" style={{backgroundColor:"#299078"}}>
                                                        REGISTER NOW
                                                    </button>
                                                </div>
                                            </div>
                                        </form>
                                        <p className="mt-4 d-flex justify-content-center ml-4">
                                            <div>
                                                Have an account?
                                            </div>


                                            <Link to='login'
                                                className="ml-2 font-semibold" style={{color:"#299078"}}>Login
                                                here</Link>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer />
        </>
    );

    const showSuccess = () => {
        if (success) return (
            <Redirect to='/profile' />
        )
    }

    const showError = (error) => {
        if (error) return (
            notify(`${error}`)
        )
    }

    return (
        <div>
            {isAuthenticated() ? <Redirect to="/" /> : ""}
            {showSuccess()}
            {showError(error)}
            {signUpForm()}
            <hr />
        </div>
    );
}

export default Register;